@import '../../../styles/variables';
@import '../../../styles/mixims';

.container {
  display: flex;
  cursor: pointer;
  transform: translateY(0);
  transition: all 0.3s ease-in-out;
  width: 330px;
  height: auto;

  &:hover {
    transform: translateY(-10px);
  }

  .blue {
    @include skillCard(rgb(108, 108, 229), rgba(108, 108, 229, 0.5) 0 5px 20px 0);
  }

  .yellow {
    @include skillCard(rgb(249, 215, 76), rgba(249, 215, 76, 0.5) 0 5px 20px 0);
  }

  .red {
    @include skillCard(rgb(249, 123, 139), rgba(249, 123, 139, 0.5) 0 5px 20px 0);
  }
}

.icon {
  width: 70px;
  height: 70px;
}

.title {
  text-align: center;
}

.desc {
}
